import CryptoJS from 'crypto-js'

const getCryptKey = () => {
  const password = window.prompt('è¯·è¾“å…¥å¯†ç ï¼š')

  if (!password) {
    throw new Error('æœªè¾“å…¥å¯†ç ')
  }

  return CryptoJS.MD5(password).toString().substr(0, 16)
}

const encrypt = content => {
  let key = getCryptKey()
  let iv = key

  key = CryptoJS.enc.Utf8.parse(key)
  iv = CryptoJS.enc.Utf8.parse(iv)

  const encrypted = CryptoJS.AES.encrypt(content, key, {
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7
  })

  return encrypted.toString()
}

const decrypt = content => {
  let key = getCryptKey()
  let iv = key

  key = CryptoJS.enc.Utf8.parse(key)
  iv = CryptoJS.enc.Utf8.parse(iv)

  const decrypted = CryptoJS.AES.decrypt(content.trim(), key, {
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7
  })

  const result = CryptoJS.enc.Utf8.stringify(decrypted)
  if (!result) {
    throw new Error('è§£å¯†å¤±è´¥ï¼ï¼ï¼')
  }

  return result
}

export default {
  read: (path, call, ecall) => {
    fetch(`/api/file?path=${encodeURIComponent(path)}`).then(response => {
      response.json().then(result => {
        if (result.status === 'ok') {
          try {
            let content = result.data
            if (path.endsWith('.c.md')) {
              content = decrypt(content)
            }

            call(content)
          } catch (e) {
            if (ecall) {
              ecall(e)
            } else {
              throw e
            }
          }
        }
      })
    })
  },
  write: (path, content, call, ecall) => {
    try {
      if (path.endsWith('.c.md')) {
        content = encrypt(content)
      }
    } catch (e) {
      if (ecall) {
        ecall(e)
      } else {
        throw e
      }
    }

    fetch('/api/file', {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({path, content})
    }).then(response => {
      response.json().then(result => {
        if (result.status === 'ok') {
          call(result)
        }
      })
    })
  },
  move: (oldPath, newPath, call) => {
    fetch('/api/file', {
      method: 'PATCH',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({oldPath, newPath})
    }).then(response => {
      response.json().then(result => {
        if (result.status === 'ok') {
          call(result)
        }
      })
    })
  },
  tree: call => {
    fetch('/api/tree').then(response => {
      response.json().then(result => {
        if (result.status === 'ok') {
          call(result.data)
        }
      })
    })
  },
  delete: (path, call) => {
    fetch(`/api/file?path=${encodeURIComponent(path)}`, {method: 'DELETE'}).then(response => {
      response.json().then(result => {
        if (result.status === 'ok') {
          call(result)
        }
      })
    })
  },
  upload: (belongPath, file, call) => {
    const fr = new FileReader()
    fr.readAsBinaryString(file)
    fr.onloadend = () => {
      const filename = CryptoJS.MD5(CryptoJS.enc.Latin1.parse(fr.result)).toString().substr(0, 8) +
        file.name.substr(file.name.lastIndexOf('.'))

      const formData = new FormData()
      formData.append('path', belongPath.replace(/\\/g, '/').replace(/\/([^/]*)$/, '/FILES/$1/' + filename))
      formData.append('attachment', file)

      fetch('/api/attachment', {
        method: 'POST',
        body: formData
      }).then(response => {
        response.json().then(result => {
          if (result.status === 'ok') {
            call(result.data)
          }
        })
      })
    }
  }
}



